import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import { CircleDefinition } from '../../../provider/connections/CircleDataTypes';
import { circlePermissionLevels } from '../../../provider/permission/permissionLevels';
import PermissionFlagsEditor from '../../Form/PermissionFlagsEditor';
import Cog from '../../Icons/Cog/Cog';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import Section from '../../Sections/Section';

const Settings = () => {
  return (
    <>
      <PageMeta icon={Cog} title={`${t('Settings')}`} />
      <AllConnectionSettings />
    </>
  );
};

const AllConnectionSettings = () => {
  const [newCircleDefinition, setNewCircleDefinition] = useState<CircleDefinition>({
    name: 'All',
    description: '',
    permissions: { keys: [0] },
    driveGrants: [],
  });

  return (
    <Section title={t('Connection access')}>
      <div className="mb-5 flex flex-row">
        <label htmlFor="name" className="my-auto mr-2">
          {t('Everyone can:')}
        </label>
        <PermissionFlagsEditor
          className="ml-auto"
          permissionLevels={circlePermissionLevels}
          defaultValue={newCircleDefinition.permissions.keys ?? [0]}
          onChange={(newValue) => {
            setNewCircleDefinition({
              ...newCircleDefinition,
              permissions: { keys: newValue },
            });
          }}
        />
      </div>
      <div className="mb-5 flex flex-row">
        <label htmlFor="name" className="my-auto mr-2">
          {t('Connections can:')}
        </label>
        <PermissionFlagsEditor
          className="ml-auto"
          permissionLevels={circlePermissionLevels}
          defaultValue={newCircleDefinition.permissions.keys ?? [0]}
          onChange={(newValue) => {
            setNewCircleDefinition({
              ...newCircleDefinition,
              permissions: { keys: newValue },
            });
          }}
        />
      </div>
    </Section>
  );
};

export default Settings;
